.form {
  display: block;
  flex-direction: column;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: large;
}
.label {
  margin-right: 10px;
}

.button {
  margin-top: 20px;
  transition: 0.2s;
  cursor: pointer;
  outline: transparent;
  background-color: transparent;
  border: transparent;
  color: rgb(255, 255, 255);
  width: 50px;
  height: 50px;
}
.button:hover {
  color: rgb(53, 167, 110);
}
.button:active {
  color: rgb(33, 147, 90);
}

span {
  display: block;
  margin: 0;
  margin-bottom: 5px;
  margin-top: 5px;
  font-weight: 500;
}
